.seo-block-wrap {
  position: relative;
  padding-top: 10px;
  margin-bottom: 30px;
  width: 100%;
  .map-wrap {
    position: relative;

  }
  .btn-map-wrap {
    position: relative;
    .btn-map-open {
      position: absolute;
      top: 10px;
      right: 50%;
      background: rgba(255,255,255,.9);
      width: 160px;
      margin-right: -80px;
      z-index: 80;
      padding: 3.5px 15px 3.5px 40px;
      &::before {
        font-size: 20px;
        position: absolute;
        left: 5px;
        top: 50%;
        margin-top: -10px;
      }
    }
  }
  #map-canvas {
    width: 100%;
    height: 255px;
    background: #fff;
  }
  @media (min-width: $screen370) {
    float: left;
    .btn-map-wrap {
      .btn-map-open {
        right: 10px;
        margin-right: 0;
      }
    }
  }
}
.seo-block {
  background: #fff;
  margin: 0;
  padding: 20px 20px 40px;
  font-size: 12px;
  line-height: 1.667;
  position: relative;
  clear: both;
  .seo-toggle {
    font-size: 20px;
    bottom: 20px;
    left: 50%;
    margin-left: -10px;
    position: absolute;
    color: inherit;
  }
  h1 {
    margin: 5px 0 14px;
    color: #333;
    font-weight: 400;
    font-size: 14px;
  }
  p {
    margin-bottom: 23px;
  }
  @media (min-width: $screen370) {
    font-size: 14px;
    h1 {
      text-align: center;
      font-size: 24px;
      line-height: 1.25;
      margin: 15px 0 14px;
    }
  }
  @media (min-width: $container-width-2) {
    h1, p {
      max-width: 720px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}